@font-face {
  font-family: "CeraPro";
  src: url("../fonts/CeraPro-Light.eot") format("embedded-opentype"), url("../fonts/CeraPro-Light.ttf") format("truetype"), url("../fonts/CeraPro-Light.woff") format("woff"), url("../fonts/CeraPro-Light.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 200;
  font-display: swap;
}
@font-face {
  font-family: "CeraPro";
  src: url("../fonts/CeraPro-Medium.eot") format("embedded-opentype"), url("../fonts/CeraPro-Medium.ttf") format("truetype"), url("../fonts/CeraPro-Medium.woff") format("woff"), url("../fonts/CeraPro-Medium.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "CeraPro";
  src: url("../fonts/CeraPro-Regular.eot") format("embedded-opentype"), url("../fonts/CeraPro-Regular.ttf") format("truetype"), url("../fonts/CeraPro-Regular.woff") format("woff"), url("../fonts/CeraPro-Regular.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "CeraPro";
  src: url("../fonts/CeraPro-Bold.eot") format("embedded-opentype"), url("../fonts/CeraPro-Bold.ttf") format("truetype"), url("../fonts/CeraPro-Bold.woff") format("woff"), url("../fonts/CeraPro-Bold.woff2") format("woff2");
  /* Super Modern Browsers */
  font-weight: 800;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-appearance: none;
}

a,
a:active,
a:visited {
  color: #1E1744;
  text-decoration: none;
}

.sectionInner {
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  height: 100%;
}

.btn {
  width: 181px;
  height: 40px;
  background-color: #F40808;
  color: #FFFFFF;
  font-family: "CeraPro", sans-serif;
  font-weight: 800;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
}
.btn:hover {
  background-color: #e00707;
}
.btn:active {
  background-color: #F40808;
}

.disable {
  opacity: 0.5;
}

.sectionHeader_firstLine {
  border: none;
  border-bottom: 1px solid rgba(30, 23, 68, 0.1);
  height: 50px;
}
.sectionHeader_firstLine .sectionInner {
  padding: 0 43px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstLine-nav {
  font-family: "CeraPro", sans-serif;
  font-weight: 500;
  font-size: 16px;
  width: 967px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstLine-location {
  width: 390px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_firstLine-locationItem {
  font-family: "CeraPro", sans-serif;
  font-weight: 200;
  font-size: 16px;
  color: #1E1744;
  margin-right: 5px;
}
.sectionHeader_firstLine-locationItem::before {
  display: inline-block;
  margin-right: 5px;
  content: " ";
  width: 12px;
  height: 17px;
  background: transparent url(../img/svg/locationIcon.svg) 50% 50%/cover no-repeat;
}
.sectionHeader_firstLine-locationItem:first-child {
  margin-left: 3px;
}
.sectionHeader_firstLine-phone {
  color: #1E1744;
  font-family: "CeraPro", sans-serif;
  font-weight: 800;
  font-size: 18px;
}
.sectionHeader_firstLine-language {
  font-family: "CeraPro", sans-serif;
  font-weight: 500;
  font-size: 16px;
  color: #1E1744;
  text-transform: uppercase;
}
.sectionHeader_secondLine {
  margin-top: 15px;
  margin-bottom: 40px;
}
.sectionHeader_secondLine .sectionInner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-right: 95px;
}
.sectionHeader_secondLine-logo {
  width: 79px;
  height: 70px;
  background: transparent url(../img/svg/logo.svg) 50% 50%/cover no-repeat;
}
.sectionHeader_secondLine-nav {
  margin-left: 70px;
  width: 1150px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_secondLine-navItem {
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
}
.sectionHeader_secondLine-navItem:last-child {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_secondLine-navItem:last-child:hover .sectionHeader_secondLine-navItemList {
  display: block;
}
.sectionHeader_secondLine-navItem:last-child::after {
  margin-left: 10px;
  display: inline-block;
  content: "";
  width: 16px;
  height: 9px;
  background: transparent url(../img/svg/back.svg) 50% 50% no-repeat;
  background-size: contain;
}
.sectionHeader_secondLine-navItemList {
  display: none;
  list-style: none;
  position: absolute;
  top: 20px;
  right: -60px;
  z-index: 20;
  min-width: 150px;
  background: #fff;
}
.sectionHeader_secondLine-navItemList li {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 30px;
  border: none;
  border-bottom: 1px solid rgba(30, 23, 68, 0.3);
}
.sectionHeader_secondLine-navItemList li .sectionHeader_secondLine-navItem {
  margin: 5px;
}
.sectionHeader_secondLine-navItemList li .sectionHeader_secondLine-navItem::after {
  display: none;
}
.sectionHeader_secondLine-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 240px;
}
.sectionHeader_secondLine-buttonsPhone {
  margin-left: 50px;
  width: 40px;
  height: 40px;
  background: transparent url(../img/svg/phoneBlue.svg) 50% 50% no-repeat;
  border: 1px solid #1E1744;
  border-radius: 5px;
}
.sectionHeader_secondLine-buttons .btn {
  margin-left: 20px;
}

.sectionSlider {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.sectionSlider .sectionInner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.sectionSlider_container {
  overflow: hidden;
  position: relative;
}
.sectionSlider_container-title {
  z-index: 5;
  width: 560px;
  height: 605px;
  font-family: "CeraPro", sans-serif;
  font-weight: 200;
  font-size: 38px;
  color: #1E1744;
  background-color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 41px;
}
.sectionSlider_container-title p span {
  font-weight: 400;
}
.sectionSlider_container-title .slideNumb {
  position: absolute;
  top: 49px;
  left: 41px;
  width: 164px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionSlider_container-title .Numb {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
}
.sectionSlider_container-title .Numb .currendSlide {
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 28px;
  align-self: flex-start;
}
.sectionSlider_container-title .Numb .divider,
.sectionSlider_container-title .Numb .allSlides {
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 18px;
}
.sectionSlider_container-bottomContent {
  width: 1155px;
  height: 147px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  z-index: 10;
}
.sectionSlider_container-bottomContentSMI {
  overflow: hidden;
  width: 164px;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: transparent url(../img/png/smi.png) 50% 50% no-repeat;
}
.sectionSlider_container-bottomContentSMI .content {
  position: relative;
  color: white;
  z-index: 10;
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 27px;
}
.sectionSlider_container-bottomContentSMI::before {
  content: "";
  position: absolute;
  z-index: 5;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(244, 8, 8, 0.85);
}
.sectionSlider_container-bottomContentEvents {
  background-color: #1E1744;
  width: 396px;
  height: 100%;
  color: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-decoration: underline;
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 27px;
}
.sectionSlider_container-bottomContentBreakfast {
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 27px;
  width: 294px;
  height: 100px;
  border: none;
  border-right: 1px solid rgba(30, 23, 68, 0.12);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.sectionSlider_container-bottomContentNews {
  padding: 0 46px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  flex-grow: 2;
}
.sectionSlider_container-bottomContentNews time {
  font-family: "CeraPro", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #3D5695;
}
.sectionSlider_container-bottomContentNews .content {
  font-family: "CeraPro", sans-serif;
  font-weight: 500;
  font-size: 17px;
  color: #1E1744;
  width: 210px;
}
.sectionSlider_track {
  display: flex;
  transition: 0.3s;
}
.sectionSlider_track-item:nth-child(3n+1) {
  background-color: yellow;
}
.sectionSlider_track-item:nth-child(3n+2) {
  background-color: green;
}
.sectionSlider_track-item:nth-child(3n+3) {
  background-color: blue;
}
.sectionSlider_track-item img {
  width: 100%;
  height: 100%;
}
.sectionSlider .btnPrev,
.sectionSlider .btnNext {
  display: block;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: transparent;
  color: #1E1744;
  outline: none;
  border: 2px solid rgba(30, 23, 68, 0.3);
  font-size: 30px;
  cursor: pointer;
}
.sectionSlider .btnPrev {
  right: calc(100% - 75px);
}
.sectionSlider .btnPrev:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.sectionSlider .btnNext {
  left: calc(100% - 75px);
}
.sectionSlider .btnNext:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

@media (max-width: 1600px) {
  .sectionHeader_firstLine .sectionInner {
    padding: 0 20px;
  }
  .sectionHeader_firstLine-nav {
    font-size: 15px;
    width: 880px;
  }
  .sectionHeader_firstLine-location {
    padding: 0 10px;
  }
  .sectionHeader_firstLine-locationItem {
    font-size: 13px;
  }
  .sectionHeader_firstLine-phone {
    width: 170px;
    font-size: 16px;
    margin-right: 10px;
  }

  .sectionSlider_container-title {
    height: 460px;
    font-size: 30px;
  }
}
@media (max-width: 1280px) {
  .sectionHeader_firstLine-phone {
    width: 215px;
    font-size: 15px;
    margin-right: 11px;
  }
  .sectionHeader .sectionHeader_secondLine-nav {
    width: 50%;
  }

  .sectionSlider_container-title {
    height: 320px;
    width: 327px;
    font-size: 20px;
    align-items: flex-start;
    padding-top: 60px;
  }
  .sectionSlider_container-title .slideNumb {
    top: 10px;
  }
  .sectionSlider_container-bottomContent {
    width: 800px;
    height: 120px;
  }
  .sectionSlider_container-bottomContentSMI .content {
    font-size: 25px;
  }
  .sectionSlider_container-bottomContentEvents {
    font-size: 25px;
  }
  .sectionSlider_container-bottomContentBreakfast {
    font-size: 25px;
    padding-left: 20px;
  }
}